body {
    margin: 0;
    padding: 0;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto","Oxygen", 
        "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
        sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
        monospace;
}

.spinner {
    -webkit-animation: img-spin infinite 15s linear;
            animation: img-spin infinite 15s linear;
}

@-webkit-keyframes img-spin {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg)
    }

    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg)
    }
}

@keyframes img-spin {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg)
    }

    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg)
    }
}

.dayDiv {
    white-space: nowrap;
    overflow: auto;
}

.onedayDiv {
    display: inline-block;
    padding-right: 10px;
}

    .onedayDiv:not(:first-child) {
        padding-left: 5px;
        border-left: dashed 1px white;
    }

    .onedayDiv > p {
        margin: 0;
    }

    .onedayDiv > img {
        width: 30px;
    }

#forcast-main {
    position: relative;
    overflow: auto;
    background-color: rgba(0,0,0,0.3);
    text-align: center;
    padding: 10px;
}

    #forcast-main > h2 {
        margin: 0;
        padding-bottom: 5px;
        border-bottom: solid 1px white;
    }

#fivedaysdiv {
    white-space: nowrap;
    overflow: auto;
   padding:5px;
}
.fivedayOne {
    display: inline-block;
    padding-left: 10px;
    padding-right: 10px;
    border:solid 1px white;
    margin-top:5px;
    margin-left:5px;
    
}
.fivedayOne:hover{
    cursor:pointer;
}
.fivedayOne > p{
    margin:5px;
}
.fivedayOne:active{
    background-color:rgba(0,0,0,0.9);
}

#current-div {
    position: relative;
    top: 0;
    text-align: center;
}
    #current-div > p{
        margin: 5px;
        display: inline-block;
    }

    #current-div > h2, h1 {
        margin-top: 0;
        width: 100%;
        text-align: center;
    }

    #current-div > img {
        width: 100px;
    }

    #current-div > h1 {
        font-size: 70px;
        font-weight: lighter;
        margin-top: 0;
        margin-bottom: 0;
    }

#otherCurrentdata > p {
    font-size:15px;
    display: inline-block;
    padding:5px;
}

    #otherCurrentdata > p:not(:first-child){
        border-left: solid 1px;
    }

html, body {
    width: 100%;
    height: 100%;
}

#app {
    position: absolute;
    width:50%;
    padding-left:10px;
    padding-right:10px;
    left: 50%;
    top: 50%;
    -webkit-transform: translate(-50%,-50%);
            transform: translate(-50%,-50%);
    background-color:cornflowerblue;
}

p, td, h1, h2 {
    color: whitesmoke;
}
@media only screen and (max-width: 600px) {
    #app{
        width:100%;
        height:100%;
        padding:0;
        overflow:auto;
    }
}
